
import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import QrCode from '../qrcode/generate/qrCode';/*gerar QrCode*/
import FormQuestion from '../form/form_question/FormQuestion'; /*formulario auditoria*/
import Products from '../form/list_product/products'; /*listar produto relacionado a celula p/ iniciar auditoria*/
import QrCodeScanner from '../qrcode/scanner/scannerQrCode';/*scanner QrCode */
import Auditorias from '../auditoria/auditorias'; /*selecionar auditoria fundiao ou usinagem */
import RegisterAuditor from '../config_auditoria/auditor/register_auditor' /*cadastrar auditor */
import RegisterCelula from '../config_auditoria/celulas/register/register_celula'/*cadastrar celula */
import RegisterProduct from '../config_auditoria/produtos/register/register_produto'/*cadastrar produto */
import Login from '../login_user/index'; /*pagina inicial */
function HandleRouter() {
  return (

    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Login />} />{/* rota ok */}
        <Route path="/RegisterProduto" element={<RegisterProduct />} />{/* rota ok */}
        <Route path="/RegisterCelula" element={<RegisterCelula />} />{/*rota ok*/}
        <Route path="/RegisterAuditor" element={<RegisterAuditor />} />{/* rota ok */}
        <Route path="/Products" element={<Products />} /> {/* rota ok */}
        <Route path="/FormQuestion" element={<FormQuestion />} />
        <Route path="/QrCode" element={<QrCode />} />{/* rota ok */}

           <Route path="/QrCodeScanner" element={<QrCodeScanner />} />
        
        <Route path="/Auditorias" element={<Auditorias />} /> {/* rota ok */}
      </Routes>
    </BrowserRouter>


  );
}

export default HandleRouter;
