import './index.css';
import React, { useEffect, useState } from 'react';
import QRCode from 'react-qr-code';
import QrcodeLink from "qrcode"
import api from '../../constantes_api/api';


const QrCode = () => {

    useEffect(() => {
          const token = localStorage.getItem('sessionToken')
           api.defaults.headers.common['Authorization'] = 'Bearer ' + token
        loandCelula()
    }, [])
    const loandCelula = async () => {
           console.log('loand...')
        try {
            const response = await api.get('/celula')


            if (response.data) {
                setCelulas(response.data)
                console.log('test', response.data)
            } else {
                console.log(response)

            }
        } catch (error) {
            console.log(error)
        }
    }
    



    const [widthQR, setWidthQR] = useState(600)
    const [parametro, setParametro] = useState('');//entrada do input
    const [qrCodeValue, setQrCodeValue] = useState(null);
    const [qrcodeLink, setQrcodelink] = useState('')
    const [celulas, setCelulas] = useState([])
    const baseUrl = `/Products`




    const handleGenerateQRCode = () => {
        if (parametro) {
            //pega a variavel 'paramentro' e passa como parametro para url
            const qrValue = `${baseUrl}?param=${encodeURIComponent(parametro)}`;
            QrcodeLink.toDataURL(qrValue, {//pega a url define o tamanho  e margin
                width: widthQR,
                margin: 3

            }
                ,
                (err, url) => {
                    setQrcodelink(url)//retorna a url para baixar
                })
            setQrCodeValue(qrValue);


        }
        //setParametro('')
    };

    //GenerateQR
    return (
        <div className='containerQrcode'>

            <h2 id='h2_GenerateQR'>Gerar QRCode</h2>
            <div>
                <label style={{ color: '#ffff' }}>Width QRcode:
                    <input
                        id='widthQRcode_GenerateQR'
                        name='widthQRcode_GenerateQR'
                        type="number"
                        value={widthQR}
                        onChange={(e) => setWidthQR(e.target.value)}
                    />

                </label>
            </div>

            {
                Array.isArray(celulas) && celulas.length > 0 &&
                <>
                    <select id='celula_qrcode'
                        name='celula_qrcode'
                        className="celula_qrcode"
                        title='Selecione uma celula'
                        onChange={(e) => setParametro(e.target.value)}
                    >
                        <option value=''>Selecione uma celula</option>
                        {celulas.map((item) => (<option key={item.id_celula} value={item.celula}>{item.celula}</option>))}
                    </select>
                </>

            }

            <>
                <button className='button_GenerateQR' onClick={handleGenerateQRCode}>Gerar QR Code</button>
            </>

            {/* Exibe o QR Code gerado */}
            {qrCodeValue && (
                <div className='C_qrcode'>
                    <h3>QR Code Gerado:</h3>
                    <QRCode value={qrCodeValue} />
                    <p>URL: {qrCodeValue}</p>
                    <div>
                        <button className="button_GenerateQR" >
                            <a href={qrcodeLink} download={parametro + '.png'} > Baixar</a>
                        </button>
                    </div>
                </div>

            )}
        </div>
    );
};

export default QrCode;

