import './index.css'
import React, { useEffect, useState } from 'react'
import api from '../../constantes_api/api'
import { useLocation, useNavigate } from 'react-router-dom'


export default function Products() {
    // estilizando o H1 --------------------------// 
    const isMobile = window.innerWidth <= 768;
    const hStyle = {
        fontSize: isMobile ? '25px' : '40px',
        color: '#fff',
        textAlign: 'center',
    };

    //---------------------------------------------// 
    const navigate = useNavigate()
    const location = useLocation()
    //pegar o parametro vindo da url apos ler o QRcode 
    const queryParams = new URLSearchParams(location.search);
    const paramValue = queryParams.get('param').toUpperCase();
    useEffect(() => {
        loandProducts()
    }, [paramValue])
    
    const loandProducts = async () => {
   
        try {
            const response = await api.get('/produto/list', { params: { celula: paramValue } })
            if (response.data) {
                setProducts_api(response.data)
              
            }
           
        } catch (error) {
            console.log('ERRO ao conectar com o servidor,tente novamente mais tarde')
        }
    }
    
    
    const [products_api, setProducts_api] = useState([])
    const [product, setProduct] = useState('')
    
    


    function initAuditoria() {
        if (product) {
            navigate('/FormQuestion', { state: { product,paramValue } })
        }
    }

    return (
        <>
            <div className='container_prod_'>
                <div className='center_prod_'>
                    <p className='title_prod_' style={hStyle}>Produtos Relacionado a Celula {paramValue ? paramValue : ''}</p>

                    {
                        Array.isArray(products_api) && products_api.length > 0 ?
                        <>
                            <select id='product_'
                                name='product_'
                                className="procucts_"
                                title='Selecione um Produto'
                                onChange={(e) => setProduct(e.target.value)}
                            >
                                <option value=''>Selecione um Produto</option>
                                {products_api.map((item, i) => (<option key={i} value={item.produto}>{item.produto}</option>))}
                            </select>
                        </> :
                       <span className='span_prod_'>{products_api}</span>
                    }
                    <>
                        <button className='button_prod_' onClick={initAuditoria}>Iniciar Auditoria</button>
                    </>
                </div>
            </div>
        </>
    )
}
