import React, { useState, useEffect } from "react";
import './index.css';
import axios from "axios";
import Header from "../heard/Header";
import { useLocation,useNavigate } from 'react-router-dom';
import api from "../../constantes_api/api.js";

export default function FormQuestion() {
 const navigate = useNavigate()
 const location = useLocation();

 
 const [checkboxState, setCheckboxState] = useState({});
 
 const [selectedFiles, setSelectedFiles] = useState({});
 const [orientation, setOrientation] = useState(false)
 const [listdata, setListData] = useState([])
 const [auditor, setAuditor] = useState('')
 const turno = listdata.map(item => item.turno)
 const data = listdata.map(item => item.data)
 
 const name = localStorage?.getItem('sessionName') || ''
     




  //pegar o parametro vindo da url navigate('/product') //
  const produto = location.state || ''
  
  //^------------------------------^//
 
 
//  //pegar o parametro vindo da url apos ler o QRcode
//  const queryParams = new URLSearchParams(location.search);
//  const paramValue = queryParams.get('param');
 
 useEffect(() => {
  

    const listquetion =  async () => {
      try {
        const response = await api.get('/question');///form das quetion
        if (response.data) {
          console.log(response.data)
          setListData(response.data)
        }
   
      } catch (error) {
        navigate('/',{replace:true})
      }
    }
    listquetion()
    const handleOrientationChange = () => {
      let orientation = window.screen.orientation.type;
      if (orientation.includes('landscape')) {
        setOrientation(true)
        alert('PARA MELHOR PERFORMANCE\t\nPor favor, use o dispositivo no modo retrato.');
      }
    };


    window.addEventListener('orientationchange', handleOrientationChange);

    return () => {
      window.removeEventListener('orientationchange', handleOrientationChange);

    };
  }, [orientation]);


  // Funes para lidar com mudanas nos checkboxes (ok)
  const handleOkChange = (id, section, question, auditor) => (event) => {
    const isChecked = event.target.checked;
    setCheckboxState((prevState) => {
      if (isChecked || prevState[id]?.nok || prevState[id]?.na) {
        return {
          ...prevState,
          [id]: {
            ...prevState[id],
            question: question,
            section: section.section,
            ok: isChecked,
            nok: isChecked ? false : prevState[id]?.nok,
            na: isChecked ? false : prevState[id]?.na,
            auditor: name,
            turno: turno[0],
            celula: produto?.paramValue,
            produto: produto?.product,
            data: data[0]

          },
        };
      } else {
        const newState = { ...prevState };
        delete newState[id];
        return newState;
      }
    });
  };
  // Funes para lidar com mudanas nos checkboxes (nok)
  const handleNokChange = (id, section, question, auditor) => (event) => {
    const isChecked = event.target.checked;
    setCheckboxState((prevState) => {
      if (isChecked || prevState[id]?.ok || prevState[id]?.na) {
        return {
          ...prevState,
          [id]: {
            ...prevState[id],
            question: question,
            section: section.section,
            nok: isChecked,
            ok: isChecked ? false : prevState[id]?.ok,
            na: isChecked ? false : prevState[id]?.na,
            auditor: name,
            turno: turno[0],
            celula: produto.paramValue,
            produto: produto.product ,
            data: data[0]
          },
        };
      }
      else {
        const newState = { ...prevState };
        delete newState[id];
        return newState;
      }
    });
    if (!isChecked) {

      delete selectedFiles[id]

    }
  };
  // Funes para lidar com mudanas nos checkboxes (n/a)
  const handleNaChange = (id, section, question, auditor) => (event) => {
    const isChecked = event.target.checked;
    setCheckboxState((prevState) => {
      if (isChecked || prevState[id]?.ok || prevState[id]?.nok) {
        return {
          ...prevState,
          [id]: {
            ...prevState[id],
            question: question,
            section: section.section,
            na: isChecked,
            ok: isChecked ? false : prevState[id]?.ok,
            nok: isChecked ? false : prevState[id]?.nok,
            auditor: name,
            turno: turno[0],
            celula: produto.paramValue,
            produto: produto.product ,
            data: data[0]
          },
        };
      } else {
        const newState = { ...prevState };
        delete newState[id];
        return newState;
      }
    });
  };

  // Funo para lidar com mudanas no campo de observao
  const handleObservationChange = (id) => (event) => {
    setCheckboxState((prevState) => ({
      ...prevState,
      [id]: {
        ...prevState[id],
        observation: event.target.value,
      },
    }));
  };


  // Funo para lidar com a seleo de arquivos (img)
  const handleInputFile = (questionId, event) => {

    const files = Array.from(event.target.files);
    setSelectedFiles((prevState) => ({
      ...prevState,
      [questionId]: files,
    }));
  };


  // Funo para enviar os dados do formulrio e os arquivos
  const handleToSend = async () => {

    const formData = new FormData();

    ///auditor, celula, produto,turno, ok, nok, na, observation, img,date,section,indice_question,question
    // Filtra apenas as perguntas que possuem pelo menos uma resposta `true`
    const filteredData = Object.keys(checkboxState).map((key) => ({
      auditor: checkboxState[key].auditor,
      celula: checkboxState[key].celula,
      produto: checkboxState[key].produto,
      turno: checkboxState[key].turno,
      ok: checkboxState[key].ok,
      nok: checkboxState[key].nok,
      na: checkboxState[key].na,
      observation: checkboxState[key].observation || '',
      data: checkboxState[key].data,
      section: checkboxState[key].section,
      questionId: key,
      question: checkboxState[key].question,
     }));
     

   Object.keys(selectedFiles).forEach((questionId) => {
    selectedFiles[questionId].forEach((file, index) => {
      // Crie um novo nome para o arquivo, incluindo o questionId ou outro identificador
      const newName = `${questionId}-${file.name}`;
      
      // Crie um novo arquivo com o nome atualizado
      const renamedFile = new File([file], newName, { type: file.type });
  
      // Adicione o arquivo renomeado ao FormData
      formData.append('files[]', renamedFile);

    });
  });
   
    formData.append('auditoriaData', JSON.stringify(filteredData));
    try {                                                          //upload
      const response = await api.post('/question/response', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      alert(response.data);
     
    } catch (error) {
      console.error('Erro ao fazer upload front:', error.response ? error.response.data : error.message);
      alert('Erro ao fazer upload');
    }


  };


  return (
    <>
      { Array.isArray(listdata) && listdata.length > 0 && <>
        <>
          <Header 
          celula={produto?.paramValue}  
          produto={produto?.product} 
          turno={turno[0]} 
          username={name}
          data={data[0]}/>
        </>
        <div className="form">
          {listdata.map((sectionTitle, sectionTitleIndex) => (
            <div className="container" key={sectionTitleIndex}>
              <div className="title">
                <label className="title_label">{sectionTitle.section}</label>
              </div>

              <div className="container_question">
                {sectionTitle.questions.map((question) => (
                  <div key={question.id}>
                    <div className="question">
                      <label>
                        <b>{question.id}</b> {question.question}
                      </label>
                    </div>

                    <div className="title_Result">
                      <label>Resultado</label>
                    </div>

                    <div className="container_checkbox">
                      <label>
                        <input
                          className="checkbox"
                          type="checkbox"
                          checked={checkboxState[question.id]?.nok || false}
                          onChange={handleNokChange(question.id, sectionTitle, question.question, auditor)}
                        />
                        NOK
                      </label>

                      {checkboxState[question.id]?.nok !== true ? (
                        <>
                          <label>
                            <input
                              className="checkbox"
                              type="checkbox"
                              checked={checkboxState[question.id]?.ok || false}
                              onChange={handleOkChange(question.id, sectionTitle, question.question, auditor)}
                              value={'ok'}
                            />
                            Ok
                          </label>
                          <label>
                            <input
                              className="checkbox"
                              type="checkbox"
                              checked={checkboxState[question.id]?.na || false}
                              onChange={handleNaChange(question.id, sectionTitle, question.question, auditor)}
                            />
                            N/A
                          </label>
                        </>
                      ) : (
                        <>
                          {/* Input de arquivo associado a esta pergunta */}
                          <input
                            id={`file-${question.id}`}
                            type="file"
                            style={{ display: 'none' }}
                            onChange={(event) => handleInputFile(question.id, event)}
                            multiple
                            name={`files[${question.id}][]`} // Nome dinmico para correspondncia no backend
                          />
                          <label className="labelImage"
                            htmlFor={`file-${question.id}`}
                            style={{
                              display: 'inline-block',
                              cursor: 'pointer',
                            }}
                          >
                            <img
                              src='./assets/image.png' // Substitua pelo caminho da sua imagem PNG
                              alt="Upload"
                              style={{ width: '40px', height: '40px' }} // Ajuste o tamanho conforme necessrio
                            />
                          </label>

                          {/* Exibir pr-visualizao das imagens selecionadas */}
                          {selectedFiles[question.id] && selectedFiles[question.id].length > 0 && (
                            <div className="previews">
                              {selectedFiles[question.id].map((file, index) => (

                                <div key={index} className="preview">
                                  <img className="image"
                                    src={URL.createObjectURL(file)}
                                    alt={`Preview ${index}`}
                                    style={{ width: '40px', height: '40px', margin: '5px', }}
                                  />

                                </div>
                              ))}
                            </div>
                          )}
                        </>
                      )}
                    </div>

                    <div className="text_Area">

                      <label htmlFor={`textArea-${question.id}`}>Observao:</label>
                      <textarea
                        className="textArea"
                        id={`textArea-${question.id}`}
                        name={`textArea-${question.id}`}
                        rows="4"
                        placeholder="Digite observaes aqui..."
                        value={checkboxState[question.id]?.observation || ''}
                        onChange={handleObservationChange(question.id)}
                      />

                    </div>
                  </div>
                ))}
              </div>
            </div>
          ))}

          <div className="C_button">
            <button id="button" onClick={handleToSend}>
              Enviar
            </button>
          </div>
        </div>
      </>}
    </>
  );
}

