import './index.css'
import React from 'react';
import { useNavigate } from 'react-router-dom';

function Auditorias() {
    const navigate = useNavigate()
    const data = new Date()
    const dataForm = {
        celula: '',
        produto: '',
        turno: '',
        auditor: '',
        data: data.toLocaleDateString()
    }
    const handleNavi = () => {
        navigate('/QrCodeScanner', { state: dataForm })
    }

    return (
        <>
            <div className='container_auditoria'>
                <div className='_auditoria' onClick={handleNavi}>
                    <img className='img_auditoria' src='./assets/audit.png' alt="Auditoria img" />
                    <b>Auditoria: Usinagem/Estamparia</b>
                </div>
                <div className='_auditoria' onClick={handleNavi}>
                    <img className='img_auditoria' src='./assets/audit.png' alt="Auditoria img" />
                    <b>Auditoria: Fundio</b>
                </div>
            </div>
        </>
    )
}

export default Auditorias