
import api from '../../../constantes_api/api'



async function creatCell(celula) {

    try {
        const response = await api.post('/resgister/celula', { celula })

        if (response.data) { return response.data }
    } catch (error) {
        if (error.response?.data.error) {
            return error.response?.data.error
        } else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }
    }
}

async function editCell(_id, celula) {
    try {
        const response = await api.put(`/update/celula/${_id}`, {celula })
        if (response.data) {
            return response.data
        }
    } catch (error) {
        if (error.response?.data.error) {
            return error.response?.data.error
        } else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }

    }
}


async function deletCell(id) {

    try {
        const response = await api.delete(`/drop/celula/${id}`)

        if (response.data) { return response.data }
    } catch (error) {
        if (error.response?.data.error) { return error.response?.data.error }
        else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }
    }
}

async function listCell() {

    try {
        const response = await api.get('/celula')

        if (response.data) { return response.data }

    } catch (error) {
        if (error.response?.data.error) { return error.response?.data.error }
        else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }
    }
}


export default { creatCell, editCell, deletCell, listCell }