import './index.css';
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import services from '../data_product/services.js';


function RegisterProduct() {

    useEffect(() => {
        handleLoandCell()
        handleListar()
    }, [])


    const navigate = useNavigate()
    // estilizando o H1 --------------------------// 
    const isMobile = window.innerWidth <= 768;
    const hStyle = {
        fontSize: isMobile ? '40px' : '50px',
        color: '#fff',
        textAlign: 'center',
    };

    const text = {
        fontSize: '18px',
    }
    //---------------------------------------------// 


    const [msg, setMsg] = useState('')
    const [opacity, setOpacity] = useState(0)


    const [isModalOpenEdit, setIsModalOpenEdit] = useState(false);
    const [isModalOpenDelet, setIsModalOpenDelet] = useState(false);
    const [listCelula, setListCelula] = useState([])//api loand_celulas
    const [idcelula, setIdCelula] = useState(0)//id celula
    const [produto, setProduto] = useState('')//input produto
    const [list_produto, setListProduto] = useState([])
    // VARIAVEIS MODAL EDIT 
    const [edit_produto, setEditProduto] = useState('')//input modal edit_produto
    const [edit_celula, setEditCelula] = useState([])//celula p/ modal edit
    const [edit_Idcelula, setEditIdCelula] = useState(0)//id_celula p/ modal edit
    const [idproduto, setIdProduto] = useState(0)//id produto


    // function mensage //
    function setAlert(m) {
        setOpacity(1)
        setMsg(m)
        setTimeout(() => {
            setMsg('')
            setOpacity(0)
        }, 3000)
    }
    //listar celula//
    async function handleLoandCell() {
        console.log('loand...')
        const cells = await services.loandCelula()
        if (cells === 'Token no informado') {
            navigate('/')
        }
        setListCelula(cells)

    }
    // cadastrar produto //
    async function handleCadastrar() {
        if (!idcelula) {
            return setAlert('Selecione uma Celula')
        } else if (!produto) {
            return setAlert('Preencha o campo Produto')
        } else {
            const Obj_produto = { produto: produto.toUpperCase(), fk_id_celula: idcelula }
            const response = await services.creatProduct(Obj_produto)
            setProduto('')
            setIdCelula('')
            handleListar()
            setAlert(response)


        }
    }

    // editar produto & celula //
    async function handleEditar(_id, _produto,fk_id_celula) {
        if (!_produto) {
            return setAlert('O campo nome no Modal  Editar no pode ser vazio')

        }
        const response = await services.editProduct(_id, _produto, fk_id_celula)
        setAlert(response)
        handleListar()

    }

    async function handleDeletar() {
        const response = await services.deletProduto(idproduto)//criar um stado para o idproduto
        setAlert(response)
        handleListar()
        setIsModalOpenDelet(false);
        setEditProduto('')
        setProduto('')

    };

    // listar produto //
    async function handleListar() {
        const response = await services.listProd()
        if (response) {
            setListProduto(response)

        } else {

            setAlert('No list, or not conect to serve')
        }


    }

    //lidar com button 'salva' do modal 'editar'//
    const btModalSave = async () => {
 
        await handleEditar(idproduto, edit_produto.toUpperCase(), edit_Idcelula)
    
        setEditProduto('')
        setProduto('')
        setEditCelula('')
        setEditIdCelula(0)
        setIsModalOpenEdit(false);//fecha modal.  

    };

    // lidar com o button 'Editar', exibir o modal no tela//
    const btModalEdit = (id_prod, prod, celula ,fk_id_celula) => {
        setIsModalOpenEdit(true);//abrir modal
        setIdProduto(id_prod)
        setEditProduto(prod)
        setEditIdCelula(fk_id_celula)
        setEditCelula(celula)
    };

    //lidar com o modal,fechar o modal 'Editar' e 'Excluir'//
    const btModalClose = () => {
        setIsModalOpenEdit(false);//fecha modal 'editar'
        setIsModalOpenDelet(false)//fecha moda 'excluir'
        setIdProduto(0)
        setEditProduto('')
        setEditCelula('')
        setEditIdCelula(0)
    };

    //lidar com o button 'Excluir' , exibir o modal 'excluir' na tela   
    const btModalDelet = (id, item) => {
        setIsModalOpenDelet(true);//abrir
        setEditProduto(item)
        setIdProduto(id)


    };

    return (
        <>
            <div className='C_register_product'>
                {/*////////// MENSAGEM DE ALERT ///////////*/}
                <div className='msg_product'>
                    {opacity === 1 &&
                        <div className='msg' >
                            <h1 style={text}>{msg}</h1>
                        </div>
                    }
                </div>
                {/*////////// THE END ///////////*/}

                <div>
                    <h1 style={hStyle}>
                        Cadastrar Produto
                    </h1>
                </div>

                <div className='C_form_rg_product'>
                    <div className='list_cell_prod'>
                        {/*/////////////// listar celular /////////////////*/}
                        {
                            Array.isArray(listCelula) && listCelula.length > 0 &&
                            <div className='container_select_prod'>
                                <label htmlFor='cell_prod'>Celula:</label>
                                <select id='cell_prod'
                                    name='cell_prod'
                                    className="cell_select_prod"
                                    title='Selecione uma celula'
                                    onChange={(e) => setIdCelula(e.target.value)}
                                    value={idcelula}
                                >
                                    <option value=''>Selecione uma celula</option>
                                    {listCelula.map((item) => (<option key={item.id_celula} value={item.id_celula}>{item.celula}</option>))}
                                </select>
                            </div>
                        }
                        {/* ///////////////////////THE END//////////////////////// */}
                    </div>
                    <div className='inputs_rg_product'>

                        <div className='input_rg'>
                            <label htmlFor='nome' >Nome:</label>
                            <input id='nome' className='user' placeholder='produto'
                                onChange={(e) => setProduto(e.target.value)}
                                value={produto}

                            />
                        </div>

                    </div>

                    <div className='C_button_rg' >
                        <button type='button' onClick={handleCadastrar}>
                            Cadastrar
                        </button>
                    </div>


                </div>



                {Array.isArray(list_produto) && list_produto.length > 0 ?
                    <table>



                        <>
                            <thead>
                                <tr className="tr_c">

                          
                                    <th>PRODUTO</th>
                                    <th>CELULA</th>
                                    <th className='th_c'>AAO</th>
                                </tr>
                            </thead>
                            <tbody>
                                {list_produto.map(item => (
                                    <tr key={item.id_produto}>
                                    
                                        <td>{item.produto}</td>
                                        <td>{item.celula}</td>
                                        <td className='td_button_c'>
                                            <button type='button' className="btn-action" onClick={() => { btModalEdit(item?.id_produto, item?.produto,item?.celula,item.fk_id_celula) }} >Editar</button>
                                            <button type='button' className="btn-action btn-delete" onClick={() => { btModalDelet(item?.id_produto, item?.produto) }}  >Excluir</button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </>

                    </table> :
                    <div style={{ display: 'flex', alignItems: 'center', height: '100px' }}>

                        <span style={{ textAlign: 'center', margin: '10px' }}>OPS... Nenhum dados para ser listado, ou nao foi possivel conectar com o servidor</span>
                    </div>
                }

                {/*//////////////////////////// modal editar ///////////////////////////////////*/}

                {isModalOpenEdit && (
                    <div className='modal_edit_prod'>
                        <div className='container_modal_edit_prod'>
                            <h2 id='title_modal_edit_prod'>Editar Item</h2>

                            <div className='container_inputs_modal_edit'>

                                
                                    {/*/////////////// listar celular /////////////////*/}
                                    {
                                        Array.isArray(listCelula) && listCelula.length > 0 &&
                                        <div className='container_select_prod_edit'>
                                            <label htmlFor='select_cell_edit' >Celula:</label>


                                            <select id='select_cell_edit'
                                                name='cell_prod'
                                                className="cell_select_prod"
                                                onChange={(e) => setEditIdCelula(e.target.value)}
                                               
                                            >

                                                <option value={edit_Idcelula}>{edit_celula}</option>
                                                {listCelula.map((item) => (<option key={item.id_celula} value={item.id_celula}>{item.celula}</option>))}
                                            </select>
                                        </div>
                                    }
                                    {/* ///////////////////////THE END//////////////////////// */}
                              

                                <div className='container_input_modal_edit_prod'>
                                    <label htmlFor='input_modal_edit_prod' >
                                        Nome:
                                        </label>
                                        <input
                                            id='input_modal_edit_prod'
                                            type="text"
                                            value={edit_produto}
                                            onChange={(e) => setEditProduto(e.target.value)}
                                            
                                            />
                                </div>
                            </div>
                            <br />
                            <div className='container_button_edit_prod' >
                                <button id='bt_salvar_prod' className='bt_modal_prod' onClick={btModalSave}>Salvar</button>
                                <button id='bt_cancelar_editar_prod' className='bt_modal_prod ' onClick={btModalClose}>Cancelar</button>

                            </div>
                        </div>
                    </div>
                )}{/*/////////////////////////// fim do modal /////////////////////////*/}




                {/*//////////////////////////// modal excluir ///////////////////////////////////*/}

                {isModalOpenDelet && (
                    <div className='modal_edit_prod'>
                        <div className='container_modal_edit_prod'>
                            <h2 id='title_modal_edit_prod'>Excluir Item</h2>
                            <div className='container_input_modal_edit_prod'>
                                <span>Deseja realmente Excluir: ' {edit_produto} '</span>
                            </div>
                            <br />
                            <div className='container_button_edit_prod' >
                                <button id='bt_deletar_cell' className='bt_modal_prod' onClick={handleDeletar}>Deletar</button>
                                <button id='bt_cancelar_delete_cell' className='bt_modal_prod' onClick={btModalClose}>Cancelar</button>

                            </div>
                        </div>
                    </div>
                )}
                {/*/////////////////////////// fim do modal /////////////////////////*/}


            </div>
        </>
    )
}


export default RegisterProduct