import axios from "axios";

 const token = localStorage.getItem('sessionToken')
   const devUrl = 'https://192.168.0.102:3003/auditoria'
  const api = axios.create({
    baseURL: devUrl,
    headers: {
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin': '*',
    }
    
  })   
  //'https://192.168.0.102:3003/auditoria'


 if(token){
   api.defaults.headers.common['Authorization'] = 'Bearer ' + token 
   
   api.defaults.headers.common['Access-Control-Allow-Methods']= 'GET, PUT,POST, DELETE'
 }



 export default api