
import api from '../../../constantes_api/api'

const loandCelula = async () => {

    try {
        const response = await api.get('/celula')


        if (response.data) {
            return response.data
        }
    } catch (error) {
        if (error.response?.data.error) {
            return error.response?.data.error
        } else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }
    }
}

async function creatProduct(produto) {
    try {
        if (!produto) {
           return  
        }else{

            const response = await api.post('/resgister/produto', produto)
            
            if (response.data) { return response.data }
        }
    } catch (error) {
        if (error.response?.data.error) {
            return error.response?.data.error
        } else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }
    }
}

async function editProduct(_id,produto, fk_id_celula) {
    console.log(fk_id_celula)
    try {
        const response = await api.put(`/update/produto/${_id}`, {produto:produto,fk_id_celula:fk_id_celula})
        if (response.data) {
            return response.data
        }
    } catch (error) {
        if (error.response?.data.error) {
            return error.response?.data.error
        } else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }

    }
}


async function deletProduto(id) {

    try {
        const response = await api.delete(`/drop/produto/${id}`)

        if (response.data) { return response.data }
    } catch (error) {
        if (error.response?.data.error) { return error.response?.data.error }
        else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }
    }
}

async function listProd() {

    try {
        const response = await api.get('/produto')

        if (response.data) { return response.data }

    } catch (error) {
        if (error.response?.data.error) { return error.response?.data.error }
        else {
            return error.message + ': servidor indisponivel tente novamente mais tarde'
        }
    }
}


export default {loandCelula, creatProduct, listProd, editProduct,deletProduto}