import './index.css';
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import services from '../data_cell/services.js';


function RegisterCelula() {

    useEffect(() => {
        handleListar();
    }, [])


    const navigate = useNavigate()
    // estilizando o H1 --------------------------// 
    const isMobile = window.innerWidth <= 768;
    const hStyle = {
        fontSize: isMobile ? '40px' : '50px',
        color: '#fff',
        textAlign: 'center',
    };

    const text = {
        fontSize: '18px',
    }
    //---------------------------------------------// 


    const [msg, setMsg] = useState('')
    const [opacity, setOpacity] = useState(0)


    const [idcell, setIdCell] = useState('')
    const [isModalOpenEdit, setIsModalOpenEdit] = useState(false);
    const [isModalOpenDelet, setIsModalOpenDelet] = useState(false);
    const [celula, setCelula] = useState('')//input celula
    const [celula_delet, setCelulaDelet] = useState('')
    const [listcelula, setListCelula] = useState([])

    // function mensage //
    function setAlert(m) {
        setOpacity(1)
        setMsg(m)
        setTimeout(() => {
            setMsg('')
            setOpacity(0)
        }, 3000)
    }

    // cadastrar celula //
    async function handleCadastrar() {
        if (!celula) {
            return setAlert('Preencha o campo celula ')
        } else {
            const response = await services.creatCell(celula.toUpperCase())
            setAlert(response)
            handleListar()
            setCelula('')
        }
    }

    // editar celula //
    async function handleEditar(_id, _celula) {
        if (!_celula) {
            return setAlert('operao no sucedida, valor no pode ser vazio')

        }
        const response = await services.editCell(_id, _celula)
        setAlert(response)
        handleListar()
    }

    async function handleDeletar() {
        setIsModalOpenDelet(false);
        const response = await services.deletCell(idcell)
        setAlert(response)
        handleListar()
        setIdCell('')
        setCelulaDelet('')
    };

    // listar celula //
    async function handleListar() {

        const response = await services.listCell()
         
        if (response === 'Token no informado') {
            navigate('/')
        }
        setListCelula(response)
        console.log(response)



    }

    //lidar com button 'salva' do modal 'editar'//
    const btModalSave = async () => {
        setIsModalOpenEdit(false);//fecha modal
        handleEditar(idcell, celula.toUpperCase())
        setIdCell('')
        setCelula('')

    };

    //lidar com o button 'Editar', exibir o modal no tela//
    const btModalEdit = (id, item) => {
        setIsModalOpenEdit(true);//abrir modal
        setCelula(item)
        setIdCell(id)

    };

    //lidar com o modal,fechar o modal 'Editar' e 'Excluir'//
    const btModalClose = () => {
        setIsModalOpenEdit(false);//fecha modal 'editar'
        setIsModalOpenDelet(false)//fecha moda 'excluir'
        setCelula('')
        setIdCell('')
        setCelulaDelet('')
    };

    //lidar com o button 'Excluir' , exibir o modal na tela  
    const btModalDelet = (id, item) => {
        setIsModalOpenDelet(true);//abrir
        setCelulaDelet(item)
        setIdCell(id)

    };

    return (
        <>
            <div className='C_register_celula'>
                <div className='msg_celula'>
                    {opacity === 1 &&

                        <div className='msg' >
                            <h1 style={text}>{msg}</h1>
                        </div>
                    }
                </div>
                <div>
                    <h1 style={hStyle}>
                        Cadastrar Celula
                    </h1>
                </div>

                <div className='C_form_rg_celula'>

                    <div className='inputs_rg'>

                        <div className='input_rg'>
                            <label htmlFor='nome' >Nome:</label>
                            <input id='nome' className='user' placeholder='celula'
                                onChange={(e) => setCelula(e.target.value)}
                                value={celula}

                            />
                        </div>

                    </div>

                    <div className='C_button_rg' >
                        <button type='button' onClick={handleCadastrar}>
                            Cadastrar
                        </button>
                    </div>


                </div>


                {Array.isArray(listcelula) && listcelula.length > 0 ?
                    <table>



                        <>
                            <thead>
                                <tr className="tr_c">

                                    <th>ID</th>
                                    <th>CELULA</th>
                                    <th className='th_c'>AAO</th>
                                </tr>
                            </thead>
                            <tbody>
                                {listcelula.map(item => (
                                    <tr key={item.id_celula}>
                                        <td>{item.id_celula}</td>
                                        <td>{item.celula}</td>
                                        <td className='td_button_c'>
                                            <button type='button' className="btn-action" onClick={() => { btModalEdit(item?.id_celula, item?.celula) }} >Editar</button>
                                            <button type='button' className="btn-action btn-delete" onClick={() => { btModalDelet(item?.id_celula, item?.celula) }}  >Excluir</button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>


                        </>
                    </table>
                    :
                    <div style={{ display:'flex',alignItems:'center',height:'100px'}}>

                    <span style={{textAlign:'center',margin:'10px'}}>OPS... Nenhum dados para ser listado, ou nao foi possivel conectar com o servidor</span>
                    </div>
                }



                {/*//////////////////////////// modal editar ///////////////////////////////////*/}

                {isModalOpenEdit && (
                    <div className='modal_edit_cell'>
                        <div className='container_modal_edit_cell'>
                            <h2 id='title_modal_edit_cell'>Editar Item</h2>
                            <div className='container_input_modal_edit_cell'>
                                <label id='label_edit_cell'>
                                    Nome:
                                    <input
                                        id='input_modal_edit_cell'
                                        type="text"
                                        value={celula}
                                        onChange={(e) => setCelula(e.target.value)}

                                    />
                                </label>
                            </div>
                            <br />
                            <div className='container_button_edit_cell' >
                                <button id='bt_salvar_cell' className='bt_modal_cell' onClick={btModalSave}>Salvar</button>
                                <button id='bt_cancelar_editar_cell' className='bt_modal_cell ' onClick={btModalClose}>Cancelar</button>

                            </div>
                        </div>
                    </div>
                )}{/*/////////////////////////// fim do modal /////////////////////////*/}




                {/*//////////////////////////// modal excluir ///////////////////////////////////*/}

                {isModalOpenDelet && (
                    <div className='modal_edit_cell'>
                        <div className='container_modal_edit_cell'>
                            <h2 id='title_modal_edit_cell'>Excluir Item</h2>
                            <div className='container_input_modal_edit_cell'>
                                <span>Deseja realmente Excluir: ' {celula_delet} '</span>
                            </div>
                            <br />
                            <div className='container_button_edit_cell' >
                                <button id='bt_deletar_cell' className='bt_modal_cell' onClick={handleDeletar}>Deletar</button>
                                <button id='bt_cancelar_delete_cell' className='bt_modal_cell' onClick={btModalClose}>Cancelar</button>

                            </div>
                        </div>
                    </div>
                )}
                {/*/////////////////////////// fim do modal /////////////////////////*/}


            </div>
        </>
    )
}


export default RegisterCelula