import React, { useState } from 'react';
import './index.css'
import api from '../../constantes_api/api'
import { useNavigate } from 'react-router-dom';
function RegisterAuditor() {
    const navigate = useNavigate()

     // estilizando o H1 --------------------------// 
     const isMobile = window.innerWidth <= 768;
     const hStyle = {
         fontSize: isMobile ? '40px' : '50px',
         color: '#fff',
         textAlign: 'center',
     };

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [pass, setPass] = useState('')
    const [msg, setMsg] = useState('')

    function setAlert(m) {
        setMsg(m)
        setTimeout(() => {
            setMsg('')
        }, 3000)
    }

    async function enter() {//FormQuestion, Qrcode , QrCodeScanner
        console.log('loanding.....')
        try {

            if (!name || !pass) {
                return setAlert('Preencha todos os campos ')
            } else {

                const response = await api.post('/resgister/auditor', { name, email, pass })
                if (response.data) {
                    localStorage.setItem('sessionIdAuditor', response.data.id_auditor)
                    localStorage.setItem('sessionName', response.data.name)
                    localStorage.setItem('sessionEmail', response.data.email)
                    localStorage.setItem('sessionDateCreate', response.data.date_create)
                    localStorage.setItem('sessionToken', response.data.token)
                    api.defaults.headers.common['Authorization'] = 'Bearer ' + response.data.token
                    navigate('/Auditorias', { replace: true })//obj replace para nao retorna para tela anterior
                    console.log('RESET', response.data)
                    console.log(localStorage.getItem('sessionToken'))
                }
            }
        } catch (error) {
            if (error.response?.data.error) {
                setAlert(error.response?.data.error)
            } else {
                console.log(error.message)
                setAlert(error.message + ': servidor indisponivel tente novamente mais tarde')
            }

        }

    }



    return (
        <>
            <div className='C_register'>
               
                <div className='h1_rg'>
                    <h1 style={hStyle}>
                        Cadastrar Auditor
                    </h1>
                </div>

                <div className='C_form_rg'>

                    <div className='inputs_rg'>

                        <div className='input_rg'>
                            <label htmlFor='nome' >Name:</label>
                            <input id='nome' className='user' placeholder='User'
                                onChange={(e) => setName(e.target.value)}
                                value={name}
                            />
                        </div>
                        <div className='input_rg'>
                            <label htmlFor='email' >E-mail:</label>
                            <input id='email' className='user' placeholder='E-mail'
                                onChange={(e) => setEmail(e.target.value)}
                                value={email}
                            />
                        </div>


                        <div className='input_rg'>
                            <label htmlFor='pass'>Password:</label>
                            <input id='pass' type="password" className='pass' placeholder='Password'
                                onChange={(e) => setPass(e.target.value)}
                                value={pass}
                            />
                        </div>

                    </div>

                    <div className='C_button_rg'>
                        <button onClick={enter}>
                            Cadastrar
                        </button>
                    </div>
                    {msg.length > 0 &&

                        <div className='alert_resgister'>
                            <h4>{msg}</h4>
                        </div>
                    }

                </div>


            </div>
        </>
    )
}

export default RegisterAuditor