// src/components/QRCodeScanner.js
import './index.css';
import React, { useEffect, useState } from 'react';
import { Scanner } from '@yudiel/react-qr-scanner'; // Importao nomeada do Scanner
import { useNavigate, useLocation } from 'react-router-dom';

const QRCodeScanner = () => {
  const navigate = useNavigate()
  //pegar o parametro vindo da url//
  const location = useLocation();
  const dataForm = location.state
  console.log(dataForm?.celula)
  //^------------------------------^//


  const [isScannerOn, setIsScannerOn] = useState(false); // Inicialmente desligado
  const [hasCamera, setHasCamera] = useState(false); // Estado para verificar se h cmera disponvel
  const [error, setError] = useState(''); // erro 





  useEffect(() => {  // Verificar se a cmera est disponvel

    window.navigator.mediaDevices?.enumerateDevices()
      .then((devices) => {
        const videoDevices = devices.filter(device => device.kind === 'videoinput');
        if (videoDevices.length > 0) {
          setHasCamera(true)
          console.log('device', videoDevices)
        } else {
          setError('Nenhuma cmera disponvel. Verifique suas conexes.');
        }
      })
      .catch((err) => {
        console.error('Erro ao verificar dispositivos de mdia:', err);
        setError('No foi possvel acessar os dispositivos de mdia.');
      });
  }, []);


  // Funo chamada quando um QR Code  decodificado com sucesso
  const handleDecode = (decodedText) => {
    try {
      let array = decodedText
      let url = array[0]
      if (url !== '') {
        setIsScannerOn(false); // Desativa o scanner antes de navegar
        //window.location.href = url;
        navigate(url)
      }
    } catch (error) {
      console.log('esse erro catch', error)
    }
  };

  //funao  chamada se houver erro 
  const handleError = (err) => {
    console.error('Erro ao escanear QR Code:', err);
    setError('No foi possvel acessar a cmera. Verifique as permisses.');
  };

  return (
    <div className='conteiner_scann'>
      {error && (
        <div style={styles.error}>
          <p>{error}</p>
        </div>
      )}


      {hasCamera && isScannerOn && (
        <div className='c_home_scann'>
          <Scanner
            onScan={(result) => handleDecode(result.map(item => item.rawValue))}
            onError={handleError}
            constraints={{ facingMode: 'environment' }} // Usar a cmera traseira

          />
        </div>


      )}

      <button className='button_scann' onClick={() => setIsScannerOn(!isScannerOn)}>
        {isScannerOn ? 'Desligar Scanner' : 'Iniciar Auditoria'}
      </button>
      {!isScannerOn ? (
        <>
          <div className='container_menu_scann' >

            <button className='button_menu_scann' onClick={() => navigate('/QrCode')}>
              Geral Qrcoce
            </button>
            <button className='button_menu_scann' onClick={() => navigate('/RegisterCelula')}>
              Registrar Celula
            </button>
            <button className='button_menu_scann' onClick={() => navigate('/RegisterProduto')}>
              Registrar Produto
            </button>
            <button className='button_menu_scann' onClick={() => navigate('/RegisterAuditor')}>
              Registrar Auditor
            </button>

          </div>
        </>
      ) : ('')

      }
    </div>
  );

};

// Objeto que contm os estilos em linha para o componente
const styles = {
  container: {
    height: '300px',
    width: '300px',
    textAlign: 'center',          // Centraliza o texto horizontalmente
    padding: '1px',  // Adiciona espaamento interno de 20px

  },
  scanner: {
    width: '100%',                // Define a largura do scanner para 100% do continer pai
    maxWidth: '400px',            // Define a largura mxima do scanner para 400px
    margin: '0 auto',             // Centraliza o scanner horizontalmente

  },
  result: {
    marginTop: '40px',            // Adiciona margem superior de 20px
    padding: '10px',              // Adiciona espaamento interno de 10px
    border: '1px solid #4caf50',  // Define uma borda verde ao redor do continer
    borderRadius: '5px',          // Arredonda os cantos do continer
    backgroundColor: '#e8f5e9',   // Define uma cor de fundo verde-claro
  },
  error: {
    marginTop: '20px',
    padding: '10px',
    border: '1px solid #f44336',
    borderRadius: '5px',
    backgroundColor: '#ffebee',
    color: '#f44336',

  },
};

export default QRCodeScanner;
