import React, { useState } from 'react';
import './index.css'
import api from '../constantes_api/api'
import { useNavigate } from 'react-router-dom';
function LoginUser() {
    const navigate = useNavigate()
    const [name, setName] = useState('q')
    const [pass, setPass] = useState('node')
    const [msg, setMsg] = useState('')
     
    function setAlert(m){
        setMsg(m)
        setTimeout(()=>{
         setMsg('')
        },3000)
    }

    async function enter() {//FormQuestion, Qrcode , QrCodeScanner
        console.log('loanding.....')
        try {
            
            if (!name || !pass) {
                return setAlert('Preencha os campos NAME e PASSWORD')
            } else {
                
                const response = await api.post('/login', { name, pass })
                if (response.data) {
                    localStorage.setItem('sessionIdAuditor',response.data.id_auditor)
                    localStorage.setItem('sessionName',response.data.name)
                    localStorage.setItem('sessionEmail',response.data.email)
                    localStorage.setItem('sessionDateCreate',response.data.date_create)
                    localStorage.setItem('sessionToken',response.data.token)
                 
                    navigate('/QrCodeScanner',{replace:true})//obj replace para nao retorna para tela anterior
                    console.log(response.data)
                
                    
                }
            }
        } catch (error) {
            if (error.response?.data.error) {
                console.log(error.response?.data.error)
            }else{
                console.log('teste')
                console.log(error)
                setAlert('erro ao conectar com o servidor, tente novamente mais tarde')
            }
             
        }     
        
    }



    return (
        <>
            <div className='C_login'>

                <div className='h1'>
                    <h1>
                        Auditoria Q3
                    </h1>
                </div>

                <div className='C_form'>

                    <div className='inputs'>

                        <div className='input'>
                            <label htmlFor='nome' >Name:</label>
                            <input id='nome' className='user' placeholder='User'
                                onChange={(e) => setName(e.target.value)}
                                value={name}
                            />
                        </div>

                        <div className='input'>
                            <label htmlFor='pass'>Password:</label>
                            <input id='pass' type="password" className='pass' placeholder='Password'
                                onChange={(e) => setPass(e.target.value)}
                                value={pass}
                            />
                        </div>

                    </div>

                    <div className='C_button'>
                        <button onClick={enter}>
                            ENTRAR
                        </button>
                    </div>
                    { msg.length > 0 &&

                        <div className='alert_login'>
                            <h4 className='img_login'>{msg}</h4>
                        </div>
                    }

                </div>


            </div>
        </>
    )
}

export default LoginUser